<template>
    <div>
        <DateSearch @func="changeDate"></DateSearch>
        <div class="box">
            <div class="titleBar">
              <span>文件操作统计</span>
            </div>
              <div class="list"> 
               <span v-for="(item, index) in fileNum.list" :key="item.id" @click="fileNumChange(index,item)" :class="fileNum.index == index ? 'select': ''">       
                {{ item.value }}    
                 </span>
            </div>
           <el-row>
            <el-col :span="24"><div class="grid-content bg-purple" id="fileOperCount"></div></el-col>
          </el-row>
        </div>
        <el-row  :gutter="20" >
          <el-col :span="12">
            <div class="box">
                <div class="titleBar">
                <span>文件类型统计</span>
                </div>
            <el-row >
                <el-col :span="24">
                <div class="grid-content bg-purple" id="fileTypeCount"></div></el-col>
            </el-row>
            </div>
          </el-col>
           <el-col :span="12">
            <div class="box">
                <div class="titleBar">
                <span>文件大小统计</span>
                </div>
            <el-row >
                <el-col :span="24">
                <div class="grid-content bg-purple" id="fileSizeCount"></div></el-col>
            </el-row>
            </div>
          </el-col>
        </el-row>
    </div>
</template>
<script>
import { EleResize} from "../../../utils/resize";
import DateSearch from './DateSearch'
import { getUserAct,getPieChart} from '../../../api/statictics';
export default {
    data() {
        return {
        fileNum:{
            index:0,
            list:[ {
                     id:'0',
                     value:'文件上传数'
                 },
                 {
                     id:'1',
                     value:'文件分发数'
                 }]
        },
        type:'file_upload',
        isStart:false,//判断首次是否通过日期监听,
        };
    },
    mounted(){
       this.lineChart();
       this.pieChart();
    },
    methods:{
      /**
       * 日期返回值
       */
      changeDate(msg){
         this.time = msg;
          if(this.isStart){
              this.lineChart();
               this.pieChart();
        }else{
          this.isStart = !this.isStart;
        }
      },
        
        /**
         * 文件操作统计
         */
        fileOperCount (data){
            let myChart = this.$echarts.init(document.getElementById('fileOperCount'));
            let option = {
                    grid:{
                     top:'8%',
                     left:'2%',
                     right:'2%',
                     bottom:'18%',
                     containLabel:true
                    },
                    tooltip: {
                     trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                        data: data.time,
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "文件数",
                        data: data.data,
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },  
                    }]
                };
             myChart.setOption(option);
             let resizeDiv = document.getElementById('fileOperCount')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        },
        /**
         * 文件数切换
         */
        fileNumChange(index,item){
           this.fileNum.index = index;
            if(index==0){
              this.type ='file_upload';
            }else{
              this.type ='file_distribute';
            }
            this.lineChart();
        },

        /***
         * 折线图
         */
         pieChart(){
         let params = new Object();
             params.time1 = this.time[0];
             params.time2 = this.time[1];
             params.types = 'file_type,file_size'
             getPieChart(params)
            .then(res => {
                if (res.code == 200) {
                  this.fileTypeCount(res.data.file_type);
                  this.fileSizeCount(res.data.file_size)
                }
            })
            .catch(error => {
                console.log(error)
            });
      },

         lineChart(){
            let params = new Object();
             params.time1 = this.time[0];
             params.time2 = this.time[1];
             params.types = this.type
             getUserAct(params)
            .then(res => {
                if (res.code == 200) {
                    if(this.fileNum.index==0){
                      this.fileOperCount(res.data.file_upload);
                    }else{
                       this.fileOperCount(res.data.file_distribute);
                    }
                   
                }
            })
            .catch(error => {
                console.log(error)
            });
         },
        /**
         * 文件类型统计
         */
        fileTypeCount(data){
            let myChart = this.$echarts.init(document.getElementById('fileTypeCount'));
            let option = {
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    icon: 'circle',
                    orient: 'vertical',
                    left: '60%',
                    top:'35%'
                },

                series: [
                    {
                        name: '标题',
                        type: 'pie',
                        radius: '80%',
                        center: ['40%', '50%'],
                        data: [
                            {value: data[0].value, name: data[0].name,itemStyle:{color:"#8d7fec"}},
                            {value: data[1].value, name: data[1].name,itemStyle:{color:"#5085f4"}},
                            {value: data[2].value, name: data[2].name,itemStyle:{color:"#e75fc3"}},
                            {value: data[3].value, name: data[3].name,itemStyle:{color:"#f2719a"}},
                        ],
                        label:{
                          show:false
                        },
                        labelLine: {
                         show: false
                        },
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            };
             myChart.setOption(option);
              let resizeDiv = document.getElementById('fileTypeCount')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        },
        /**
         * 文件大小统计
         */
        fileSizeCount(data){
            let total =  data[0].value+data[1].value+data[2].value+data[3].value+data[4].value
            let myChart = this.$echarts.init(document.getElementById('fileSizeCount'));
            let m2R2Data= [
                 {value:data[0].value, legendname:data[0].name,name:data[0].name,itemStyle:{color:"#8d7fec"}},
                 {value:data[1].value, legendname:data[1].name,name:data[1].name,itemStyle:{color:"#5085f2"}},
                 {value:data[2].value, legendname:data[2].name,name:data[2].name,itemStyle:{color:"#e75fc3"}},
                 {value:data[3].value, legendname:data[3].name,name:data[3].name,itemStyle:{color:"#f87be2"}},
                 {value:data[4].value, legendname:data[4].name,name:data[4].name,itemStyle:{color:"#f2719a"}}
            ];

            let option = {
                title: [
                {
                    text: '合计',
                    subtext: total+'个',
                    textStyle:{
                        fontSize:20,
                        color:"black"
                    },
                    subtextStyle: {
                        fontSize: 20,
                        color: 'black'
                    },
                    textAlign:"center",
                    x: '35%',
                    y: '44%',
                }],
                tooltip: {
                    trigger: 'item',
                    formatter:function (parms){
                        let str=  parms.seriesName+"</br>"+
                            parms.marker+""+parms.data.legendname+"</br>"+
                            "数量："+ parms.data.value+"</br>"+
                            "占比："+ parms.percent+"%";
                            return  str ;
                    }
                },
                legend: {
                    icon: 'circle',
                    type:"scroll",
                    orient: 'vertical',
                    left:'60%',
                    align:'left',
                    top:'middle',
                    textStyle: {
                        color:'#8C8C8C'
                    },
                    height:250
                },
                series: [
                    {
                        name:'标题',
                        type:'pie',
                        center: ['35%', '50%'],
                        radius: ['60%', '75%'],
                        clockwise: false, //饼图的扇区是否是顺时针排布
                        avoidLabelOverlap: false,
                        label: {
                            show:false,
                        },
                        labelLine: {
                            show: false,
                        },
                        data:m2R2Data
                    }
                ]
            };
              myChart.setOption(option);
              let resizeDiv = document.getElementById('fileSizeCount')
              let listener  =function(){
              myChart.resize();
            }
	     	  EleResize.on(resizeDiv,listener);
        }

    },
    components:{
          DateSearch
        }
}
</script>
<style lang="less" scoped>
 @import "../index.less";
 .list{
     padding: 15px 0px 0px 15px;
     span{
         padding-right: 10px;
         cursor: pointer;
     }
     .select{
        color: #409EFF;

     }
 }
</style>


