<template>
    <div>
       <DateSearch @func="changeDate"></DateSearch>
        <div class="box">
            <div class="titleBar">
              <span>拓展服务数据指标</span>
              <div class="radio-list">
               <el-radio-group v-model="radio">
                <el-radio :label="1">IP代理</el-radio>
                <el-radio :label="2">设备大师</el-radio>
                <el-radio :label="3">虚拟定位</el-radio>
                <el-radio :label="4">窗口多开</el-radio>
                <el-radio :label="5">多路实时直播</el-radio>
                <el-radio :label="6">远程虚拟相机</el-radio>
                <el-radio :label="7">日志调试</el-radio>
                <el-radio :label="8">高调试ADB</el-radio>
              </el-radio-group>
             </div>
            </div>
            <div class="list"> 
              <span v-for="(item, index) in phoneNumType.list" :key="item.id" @click="phoneNumTypeChange(index,item)" :class="phoneNumType.index == index ? 'select': ''">       
                {{ item.value }}    
                 </span>
            </div>
           <el-row>
            <el-col :span="24"><div class="grid-content bg-purple" id="extendServerData"></div></el-col>
          </el-row>
        </div>
    </div>
</template>
<script>
import { EleResize} from "../../../utils/resize";
import DateSearch from './DateSearch'
export default {
    data() {
        return {
        radio: 1,
        phoneNumType:{
            index:0,
            list:[ {
                     id:'0',
                     value:'订购台数'
                 },
                 {
                     id:'1',
                     value:'使用率'
                 },{
                     id:'2',
                     value:'到期台数'
                 },
                 {
                     id:'3',
                     value:'使用次数'
                 }]
        }
        };
    },
    mounted(){
      this.extendServerDataFun();
    },
    methods:{
       /**
         * 台数类型选择
         */
        phoneNumTypeChange(index,item){
            this.phoneNumType.index = index
        },
     /**
       * 日期返回值
       */
      changeDate(msg){
        console.log(msg);
      },
      /**
       * 拓展服务数据指标 
       */
      extendServerDataFun(){
            let myChart = this.$echarts.init(document.getElementById('extendServerData'));
            let option = {
                    grid:{
                     top:'8%',
                     left:'2%',
                     right:'2%',
                     bottom:'18%',
                     containLabel:true
                    },
                    tooltip: {
                     trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                        data: ['2021-05-22', '2021-05-23', '2021-05-24', '2021-05-25', '2021-05-26', '2021-05-27', '2021-05-28'],
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "使用人数",
                        data: [150, 230, 224, 218, 135, 147, 260],
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },     
                    }]
                };
             myChart.setOption(option);
              let resizeDiv = document.getElementById('extendServerData')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
      }
    },
     components:{
          DateSearch
        }
}
</script>
<style lang="less" scoped>
 @import "../index.less";
 .titleBar{
     line-height: 0;
     .radio-list{
        display: inline-block;    
     }
 }
  .list{
     padding: 15px 0px 0px 15px;
     display: flex;
     span{
         padding-right: 10px;
         cursor: pointer;
         border: 1px solid #ccc;
        // margin: -5px;
        padding: 5px 15px;
        font-size: 14px;
        border-right: 0px;
     }
     span:last-child{
           border-right: 1px solid #ccc;
     }
     .select{
        color: #409EFF;

     }
 }
</style>


