<template>
    <div>
        <DateSearch @func="changeDate"></DateSearch>
        <div class="box">
            <div class="titleBar">
              <span>云机控制器使用率</span>
            </div>
           <el-row :gutter="20">
            <el-col :span="12"><div class="grid-content bg-purple" id="phoneStartNum"></div></el-col>
            <el-col :span="12"><div class="grid-content bg-purple-light" id="phoneConTime"></div></el-col>
          </el-row>
        </div>
        <div class="box">
            <div class="titleBar">
              <span>云机群控使用率</span>
            </div>
           <el-row :gutter="20">
            <el-col :span="12"><div class="grid-content bg-purple" id="phoneGroupNum"></div></el-col>
            <el-col :span="12"><div class="grid-content bg-purple-light" id="phoneGroupAveNum"></div></el-col>
          </el-row>
        </div>
        <div class="box">
            <div class="titleBar">
              <span>其它操作使用率</span>
            </div>
             <div class="radio-list">
               <span>其它操作：</span>
               <el-radio-group v-model="radio" @change="radioChange">
                <el-radio :label="1">云机重启</el-radio>
                <el-radio :label="2">恢复出厂</el-radio>
                <el-radio :label="3">屏幕截图</el-radio>
                <el-radio :label="4">新建分组</el-radio>
                <el-radio :label="5">应用安装</el-radio>
                <el-radio :label="6">应用卸载</el-radio>
              </el-radio-group>
             </div>
           <el-row :gutter="20">
            <el-col :span="24">
              <div class="grid-content bg-purple" id="otherOperation"></div></el-col>
          </el-row>
        </div>
    </div>
</template>
<script>
import { EleResize} from "../../../utils/resize";
import DateSearch from './DateSearch'
import { getUserAct} from '../../../api/statictics';
export default {
    data() {
        return {
        radio:1,
        time:'',
        isStart:false,//判断首次是否通过日期监听,
        type:'act_users,using_time,group_users,group_size,reboot',
        dataList:'',
        };
    },
    mounted() {
      this.$nextTick(() => {
        this.userAct();
       
        });
     },
    
    methods: {
      /**
       * 日期返回值
       */
      changeDate(msg){
        this.time = msg;
         if(this.isStart){
             let change  ='reboot';
             let val  = this.radio;
               if(val==1){
                      change  ='reboot';
                    }else if(val==2){
                       change  ='reset';
                    }else if(val==3){
                        change  ='screenshot';
                    }else if(val==4){
                       change  ='new_group';
                    }else if(val==5){
                        change  ='apk_install';
                    }else if(val==6){
                       change  ='apk_uninstall';
                    }
             this.type = 'act_users,using_time,group_users,group_size,'+change
             this.userAct();
        }else{
          this.isStart = !this.isStart;
        }
      },
      /**
       * 单选框操作
       */
      radioChange(val){
           switch (val) {
               case 1:
                 this.type = 'reboot';
               break;
                case 2:
                 this.type = 'reset';
                
               break;
                case 3:
                 this.type = 'screenshot';
               break;
                case 4:
                 this.type = 'new_group';
               break;
                case 5:
                 this.type = 'apk_install';
               break;
                case 6:
                 this.type = 'apk_uninstall';
               break;
             default:
               break;

           }
             let params = new Object();
             params.time1 = this.time[0];
             params.time2 = this.time[1];
             params.types = this.type
             getUserAct(params)
            .then(res => {
                if (res.code == 200) {
                    if(val==1){
                        this.otherOperationFun(res.data.reboot);
                    }else if(val==2){
                        this.otherOperationFun(res.data.reset);
                    }else if(val==3){
                        this.otherOperationFun(res.data.screenshot);
                    }else if(val==4){
                        this.otherOperationFun(res.data.new_group);
                    }else if(val==5){
                        this.otherOperationFun(res.data.apk_install);
                    }else if(val==6){
                        this.otherOperationFun(res.data.apk_uninstall);
                    }
                }
            })
            .catch(error => {
                console.log(error)
            });
      },
      /**
       * 用户操作
       */
      userAct(){
          let val  = this.radio;
         let params = new Object();
             params.time1 = this.time[0];
             params.time2 = this.time[1];
             params.types = this.type
             getUserAct(params)
            .then(res => {
                if (res.code == 200) {
                   this.dataList = res.data
                    this.phoneStartNumFun();
                    this.phoneConTimeFun();
                    this.phoneGroupNumFun();
                    this.phoneGroupAveNumFun();
                    if(val==1){
                        this.otherOperationFun(res.data.reboot);
                    }else if(val==2){
                        this.otherOperationFun(res.data.reset);
                    }else if(val==3){
                        this.otherOperationFun(res.data.screenshot);
                    }else if(val==4){
                        this.otherOperationFun(res.data.new_group);
                    }else if(val==5){
                        this.otherOperationFun(res.data.apk_install);
                    }else if(val==6){
                        this.otherOperationFun(res.data.apk_uninstall);
                    }
                }
            })
            .catch(error => {
                console.log(error)
            });
      },

        /**
         * 云机启动人数
         */
        phoneStartNumFun (){
            let myChart = this.$echarts.init(document.getElementById('phoneStartNum'));
            let option = {
                  grid:{
                     top:'15%',
                     left:'3%',
                     right:'2%',
                     bottom:'8%',
                     containLabel:true
                    },
                    title: {
                        text: '云机启动人数',
                        textStyle:{
                          fontWeight: 'normal' ,
                        },
                        fontSize:16,
                        left:'2%',
                        top:10
                    },
                    tooltip: {
                     trigger: 'axis',
                    },
                    xAxis: {
                        type: 'category',
                        data: this.dataList.act_users.time,
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "云机启动人数",
                        data: this.dataList.act_users.data,
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },  
                    }]
                };
             myChart.setOption(option);
            let resizeDiv = document.getElementById('phoneStartNum')
            let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
                  
        },
        /**
         * 云机连接时长
         */
        phoneConTimeFun(){
            let myChart = this.$echarts.init(document.getElementById('phoneConTime'));
            let option = {
              grid:{
                     top:'15%',
                     left:'3%',
                     right:'2%',
                    bottom:'8%',
                     containLabel:true
                    },
                    title: {
                        text: '云机连接时长',
                        textStyle:{
                          fontWeight: 'normal' ,
                        },
                        fontSize:16,
                        left:'2%',
                        top:10
                    },
                    tooltip: {
                     trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                         data: this.dataList.using_time.time,
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "云机连接时长",
                        data: this.dataList.using_time.data,
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },  
                    }]
                };
             myChart.setOption(option);
            let resizeDiv = document.getElementById('phoneConTime')
            let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        },
         /**
         * 群控使用人数
         */
        phoneGroupNumFun(){
            let myChart = this.$echarts.init(document.getElementById('phoneGroupNum'));
            let option = {
                  grid:{
                     top:'15%',
                     left:'3%',
                     right:'2%',
                     bottom:'8%',
                     containLabel:true
                    },
                    title: {
                        text: '群控使用人数',
                        textStyle:{
                          fontWeight: 'normal' ,
                        },
                        fontSize:16,
                        left:'2%',
                        top:10
                    },
                    tooltip: {
                     trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                         data: this.dataList.group_users.time,
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "群控使用人数",
                        data:  this.dataList.group_users.data,
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },  
                    }]
                };
             myChart.setOption(option);
            let resizeDiv = document.getElementById('phoneGroupNum')
            let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        },
         /**
         * 群控平均云机数
         */
        phoneGroupAveNumFun(){
            let myChart = this.$echarts.init(document.getElementById('phoneGroupAveNum'));
            let option = {
              grid:{
                     top:'15%',
                     left:'3%',
                     right:'2%',
                     bottom:'8%',
                     containLabel:true
                    },
                    title: {
                        text: '群控平均云机数',
                        textStyle:{
                          fontWeight: 'normal' ,
                        },
                        fontSize:16,
                        left:'2%',
                        top:10
                    },
                    tooltip: {
                     trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                         data: this.dataList.group_size.time,
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "群控平均云机数",
                        data:this.dataList.group_size.data,
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },  
                    }]
                };
              myChart.setOption(option);
              let resizeDiv = document.getElementById('phoneGroupAveNum')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        },
         /**
         * 其它操作使用率
         */
        otherOperationFun(type){
            let myChart = this.$echarts.init(document.getElementById('otherOperation'));
            let option = {
                   grid:{
                     top:'8%',
                     left:'2%',
                     right:'2%',
                     bottom:'20%',
                     containLabel:true
                    },
                    tooltip: {
                     trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                         data:type.time,
                    },
                    yAxis: {
                        type: 'value',
                        axisLine:{    
                          show:false
                        },
                        axisTick:{    
                           show:false
                        },
                        splitLine: { 
                            lineStyle:{
                              type:'dashed'
                        }
                            }                                     
                    },
                    series: [{
                        name: "使用率",
                        data: type.data,
                        type: 'line',
                        itemStyle : {  
                                normal : {  
                                    color:'#409eff',  
                                    lineStyle:{  
                                        color:'#409eff'  
                                    }  
                                }  
                            },  
                    }]
                };
              myChart.setOption(option);
              let resizeDiv = document.getElementById('otherOperation')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        }
    },
    components:{
          DateSearch
        }
}
</script>
<style lang="less" scoped>
 @import "../index.less";
</style>



