<template>
    <div class="maxBox">
         <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="用户操作" name="1">
             <UserAction v-if="activeName==1"></UserAction>
            </el-tab-pane>
            <el-tab-pane label="应用安装" name="2">
             <AppInstall v-if="activeName==2"></AppInstall>
            </el-tab-pane>
            <el-tab-pane label="文件分发" name="3">
              <FileHandOut v-if="activeName==3"></FileHandOut>
            </el-tab-pane>
            <el-tab-pane label="拓展服务" name="4">
             <ExpandingServices v-if="activeName==4"></ExpandingServices>
            </el-tab-pane>
        </el-tabs>
    </div>


</template>
<script>
import UserAction from './components/UserAction';
import AppInstall from './components/AppInstall';
import FileHandOut from './components/FileHandOut';
import ExpandingServices from './components/ExpandingServices';

    export default {
    data() {
        return {
        activeName: '1'
        };
    },
    methods: {
    handleClick(tab, event) {
        this.activeName = tab.name;
        }
    },
    components: {
        UserAction,
        AppInstall,
        FileHandOut,
        ExpandingServices
    }
  };
</script>
<style lang="less" scoped>
.maxBox {
    padding: 30px;
    padding-top: 0px;
}
</style>


