<template>
    <div>
        <div class="box" style="height: 600px;">
          <div class="toolBar">
             <el-row>
            <el-col :span="4"><div class="left"> <span >安装应用排行TOP20</span></div></el-col>
            <el-col :span="24">  <div class="right">
              <el-button-group>
               <el-button @click="yesterdayClick">昨日</el-button>
               <el-button @click="lastWeekClick">近7日</el-button>
               <el-button @click="lastMonthClick">近30日</el-button>
            </el-button-group>
          <div class="block">
               <el-date-picker
                v-model="time"
                @change="selectDate"
                type="daterange"
                range-separator="~"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
                </el-date-picker>
            </div>
             </div></el-col>
          </el-row>
       </div>
           <div class="list"> 
                 <span v-for="(item, index) in appNum.list" :key="item.id" @click="appNumChange(index,item)" :class="appNum.index == index ? 'select': ''">       
                {{ item.value }}    
                 </span></div>
           <el-row>
            <el-col :span="24">
               
                <div class="grid-content " id="appRanking"></div></el-col>
          </el-row>
        </div>
        <el-row>
            <el-col :span="8">
               <div class="box">
                    <div class="titleBar">
                    <span>应用大小统计</span>
                    </div>
                <el-row >
                    <el-col :span="24">
                    <div class="grid-content " id="appSizeCount"></div></el-col>
                </el-row>
                </div>
            </el-col>
          </el-row>
    </div>
</template>
<script>
import { EleResize} from "../../../utils/resize";
import { getAppInstall,getPieChart} from '../../../api/statictics';
export default {
     data() {
        return {
        time: '',
        appNum:{
            index:0,
            list:[ {
                     id:'0',
                     type:'upload',
                     value:'应用上传数'
                 },
                 {
                     id:'1',
                     type:'install',
                     value:'应用安装数'
                 },
                 {
                     id:'2',
                     type:'using_time',
                     value:'应用使用时长'
                 }],
          
        },
        type:'upload',
        appList:'',
        };
    },
    mounted(){
    
      this.$nextTick(() => {
           this.lastWeekClick();
        });
    },
    methods:{
        format(date) {
        let str = new Date(date);
        let YY = str.getFullYear() + '-';
        let MM = (str.getMonth() + 1 < 10 ? '0' + (str.getMonth() + 1) : str.getMonth() + 1) + '-';
        let DD = (str.getDate() < 10 ? '0' + (str.getDate()) : str.getDate());
        return YY + MM + DD;
      },
      /**
       * 昨天
       */
      yesterdayClick(){
        const end = new Date();
        const start = new Date();
        const yesterday =  start.setTime(start.getTime() - 3600 * 1000 * 24);
        this.time = [this.format(yesterday),this.format(end.getTime())];
        this.appInstall();
         this.applSizeCount();
      },
      /**
       * 上周
       */
      lastWeekClick(){
        const end = new Date();
        const start = new Date();
        const  lastWeek =  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        this.time = [this.format(lastWeek),this.format(end.getTime())];
         this.appInstall();
         this.applSizeCount();
      },
      /**
       *上月
       */
      lastMonthClick(){
        const end = new Date();
        const start = new Date();
        const lastMonth =  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        this.time = [this.format(lastMonth),this.format(end.getTime())];
        this.appInstall();
        this.applSizeCount();
      },
      /**
       * 日期选择
       */
       selectDate(val){
           this.time = [this.format(val[0]),this.format(val[1])];
           this.appInstall();
           this.lastWeekClick();
         },
      /**
       * 应用安装排行
       */
      appInstall(){
         let params = new Object();
             params.time1 = this.time[0];
             params.time2 = this.time[1];
             params.type = this.type
             getAppInstall(params)
            .then(res => {
                if (res.code == 200) {
                    this.appList = res.data;
                    this.appRanking();
                }
            })
            .catch(error => {
                console.log(error)
            });
      },
       /**
         * 应用数切换
         */
        appNumChange(index,item){
           this.appNum.index = index;
           this.type = item.type;
           this.appInstall();
        },
         /**
         * 安装应用排行TOP20
         */
        appRanking (){
            let name = this.appList.name.reverse();
            let data = this.appList.data.reverse();
            let myChart = this.$echarts.init(document.getElementById('appRanking'));
            let option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                grid: {
                    top:'2%',
                    left: '3%',
                    right: '4%',
                    bottom: '10%',
                    containLabel: true
                },
                xAxis: {
                    type: 'value',
                    boundaryGap: [0, 0.01]
                },
                yAxis: {
                    type: 'category',
                    data:name
                },
                series: [
                    {
                        name: '数据',
                        type: 'bar',
                        barWidth: 15, // 柱子宽度
                        data: data,
                        itemStyle : {  
                            normal : {  
                                color:'#409eff',  
                                lineStyle:{  
                                   color:'#409eff',  
                                }  
                            }  
                        }, 
                    },
                
                ]
            };
             myChart.setOption(option);
              let resizeDiv = document.getElementById('appRanking')
              let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
        },
        /**
         * 应用大小统计
         */
        applSizeCount(){
            let params = new Object();
             params.time1 = this.time[0];
             params.time2 = this.time[1];
             params.types = 'apk_size'
             getPieChart(params)
            .then(res => {
                if (res.code == 200) {
            let data  = res.data.apk_size;
            let total = data[0].value+data[1].value+data[2].value+data[3].value
            let myChart = this.$echarts.init(document.getElementById('appSizeCount'));
            let m2R2Data= [
               {value:data[0].value, legendname:data[0].name,name:data[0].name,itemStyle:{color:"#8d7fec"}},
               {value:data[1].value, legendname:data[1].name,name:data[1].name,itemStyle:{color:"#5085f2"}},
               {value:data[2].value, legendname:data[2].name,name:data[2].name,itemStyle:{color:"#e75fc3"}},
               {value:data[3].value, legendname:data[3].name,name:data[3].name,itemStyle:{color:"#f87be2"}},
               {value:data[4].value, legendname:data[4].name,name:data[4].name,itemStyle:{color:"#f2719a"}},
               
            ];

            let  option = {
                title: [
                {
                    text: '合计',
                    subtext: total+'个',
                    textStyle:{
                        fontSize:20,
                        color:"black"
                    },
                    subtextStyle: {
                        fontSize: 20,
                        color: 'black'
                    },
                    textAlign:"center",
                    x: '35%',
                    y: '44%',
                }],
                tooltip: {
                    trigger: 'item',
                    formatter:function (parms){
                    var str=  parms.seriesName+"</br>"+
                        parms.marker+""+parms.data.legendname+"</br>"+
                        "数量："+ parms.data.value+"</br>"+
                        "占比："+ parms.percent+"%";
                        return  str ;
                    }
                },
                legend: {
                    icon: 'circle',
                    type:"scroll",
                    orient: 'vertical',
                    left:'60%',
                    align:'left',
                    top:'middle',
                    textStyle: {
                      color:'#8C8C8C'
                    },
                    height:250,
                     formatter: params => { 
                        const dataAll = m2R2Data.map(item =>item.value)
                        const total = dataAll.reduce((x, y) => parseInt(x, 10) + parseInt(y, 10))
                        const value = (m2R2Data.filter(item => item.legendname === params)[0].value)
                     
                        if (total === 0) {
                            return `{a|${params}：}{b|0.00%}`
                        }
                        const name = params
                        return "{a|" + name + "} {d||} {b|"+Number((value * 100) / total).toFixed(2)+'%'+"}   {c|"+total+"}"
                },
                 textStyle: {
                        color: "black", //元字颜色
                        fontSize: 14,
                        rich: {
                            a: {
                                color: 'black',
                                fontSize: 14,
                            },
                            b:{
                                color: 'black',
                                fontSize: 14,   
                            },
                            c:{
                                color: '#ccc',
                                fontSize: 14,
                            },
                            d:{
                                 color: '#ccc',
                            }
                        }
                    },  
                },
                series: [
                    {
                    name:'标题',
                    type:'pie',
                    center: ['35%', '50%'],
                    radius: ['60%', '75%'],
                    clockwise: false, //饼图的扇区是否是顺时针排布
                    avoidLabelOverlap: false,
                    label:{
                        show:false
                    },
                    labelLine: {
                        show: false
                    },
                    data:m2R2Data
                    }
                ]
            };
            myChart.setOption(option);
             let resizeDiv = document.getElementById('appSizeCount')
             let listener  =function(){
              myChart.resize();
            }
	     	    EleResize.on(resizeDiv,listener);
                }
            })
            .catch(error => {
                console.log(error)
            });
           
        }
    }
}
</script>
<style lang="less" scoped>
 @import "../index.less";
 .list{
     padding: 15px 0px 0px 15px;
     span{
         padding-right: 10px;
         cursor: pointer;
     }
     .select{
        color: #409EFF;
     }
 }
</style>


